@font-face {
    font-family: Monsterrat Light;
    /* Гарнитура шрифта */
    src: url(./Montserrat-Light.ttf);
    /* Путь к файлу со шрифтом */
  }

  @font-face {
    font-family: Raleway Regular;
    /* Гарнитура шрифта */
    src: url(./Raleway-Regular.ttf)
      /* Путь к файлу со шрифтом */
      /*c7b299*/
  }
  @import "reset";
  *{box-sizing: border-box;}

header{
    position: fixed ;
    top:0;
    width: 100%;
}
.header{
    padding:15px 0 ;
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
    justify-content: space-between;
    background-color:transparent;
    .logo{
        display: flex;
        justify-content: left;
        align-content: center;
      }   
  
        
    .logo img{
            height: 56px;
            width: 181px;
            object-fit: contain;
        }
   
   
    
    .nav .search-nav{
       
        ul{display: flex;
            flex-wrap: wrap;
            align-content: space-around;
            justify-content: space-around;
            list-style: none;
            li{
                padding: 10px;
                a{
                    img{
                        height: 24px;
                        width: 24px;
                        object-fit: contain;
                    }
                }
            }
        }
    }
     .nav .main-nav{
         ul{display: flex;
         flex-wrap: wrap;
         flex-direction: row;
         justify-content: end;
         list-style: none;
        li{
            padding: 10px;
            a{  
                color:white;
                text-decoration: none;
                font-size: 14pt;
                font-family:Monsterrat Light;
                letter-spacing: .01em;
                
            }
            a:hover{
                color: #c7b299;
            }
        }}
         
     }
}
.first-section{
    background-blend-mode: multiply;
    background-color: rgb(128, 128, 128);
    background-image:url('./Main/FirstSection/firstsection.png');
    
      height: 100vh;
      background-repeat: no-repeat;
      background-position:center;
      background-size: cover;

      display: flex;
      align-items: center;
      
}